#header {
	background: #2b3137;
}

.search {
	display: flex;
	justify-content: center;
	align-items: center;
}

.search-icon {
	cursor: pointer;
	filter: brightness(0) invert(1);
}

.search-box {
	border-radius: 10px;
	border: 2px solid rgb(233, 233, 233);
	font-size: 15.5px;
	height: 35px;
	width: 50vw;
	color: #2f884f;
	opacity: 0.9;
	transition: 0.3s;
}

.search-box:hover {
	opacity: 1;
}

::placeholder {
	color: #2f884f;
}

.search-box,
.search-icon {
	display: inline-block;
	margin-top: 25px;
}

.github-logo {
	opacity: 0.9;
	filter: brightness(0) invert(1);
	transition: 0.3s;
	float: left;
	width: 50px;
	margin-top: 17.5px;
	margin-left: 10px;
	position: absolute;
}

.github-logo:hover {
	opacity: 1;
}

/* Last Searches */
.last-searches {
	opacity: 1;
	color:white;
	/* background: red; */
	text-align: center;
	margin-left: auto;
	height: 7.5vh;
	
	display: flex;
	justify-items: center;
	align-items: center;
	justify-content: center;
}
/* Last Searches */

@media only screen and (max-width: 568px) {
	.last-searches {
		height: 12.5vh;
	}
}

@media only screen and (max-width: 468px) {
	.last-searches {
		height: 15vh;
	}
}

@media only screen and (max-width: 368px) {
	.last-searches {
		height: 18.5vh;
		font-size: 14.5px;
	}
}