.person-wrapper {
	background: #24292d;
	margin-top: -20px;
	opacity: 1;
}

.person-container {
	box-sizing: border-box;
	margin: 20px 70px 20px 70px;
	max-height: 91.5vh;
	overflow: scroll;
}

.person-container:after {
	content: '';
	display: block;
	clear: both;
}

.person {
	background: #ffffff;
	width: 100%;
}

.person-avatar {
	width: 100px;
	height: 100px;
	float: left;
	margin-right: 10px;
	border-radius: 11px;
}

.person-repos {
	list-style: none;
	margin-top: 11px;
	font-size: 14px;
	color: #2b3337;
}

.person-name {
	text-decoration: none;
	/* color: rgb(0, 154, 226); */
	color: #0d74e7;
}

.person-type {
	color: rgb(100, 100, 100);
}

#person-list-of-persons {
	/* header text */
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	font-weight: 200;
	letter-spacing: 2px;
	font-size: 20px;
	height: 50px;
	background-color: #d8d8d8;
	color: #2b3337;
}

.person-each-person {
	border-bottom: 1.5px solid #2b3337;
	margin: 20px;
}

@media only screen and (max-width: 568px) {
	#person-list-of-persons {
        height: 20vh;
	}
}

@media only screen and (max-width: 468px) {
	.person-name {
		zoom: 82%;
	}
	#person-list-of-persons {
		height: 25vh;
	}
	.person-repos {
		font-size: 11px;
	}
}

@media only screen and (max-width: 368px) {
	#person-list-of-persons {
		height: 55vh;
	}
	.person-repos {
		font-size: 9px;
	}
}