.search-user-page {
    /* Everything */
    background: #24292d;
    color: #ffffff;
    text-align: center;
    margin: 25px;
    min-height: 92vh;
    box-shadow: 4px 4px gray;
    font-family: monospace;

}

.search-user-page > li {
    list-style: none;
}

#search-user-img {
    width: 250px;
    border-radius: 50%;
    box-shadow: 4px 4px #2b3337;
}

#search-user-follower-following {
    filter: brightness(0) invert(1);
    font-size: 15px;
}

#follower-following-img {
    position: relative;
    top:5.25px;
    margin-right: 5px;
}

.search-user {

}

#full-name {
    margin-top:15px;
    color: #ffffff;
}

#search-user-login{
    color: #b3b3b3;
    text-decoration: underline;
    font-weight: 150;
}

.search-user-logo {
	opacity: 0.9;
	filter: brightness(0) invert(1);
	transition: 0.3s;
	width: 50px;
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    
    
}

.search-user-logo:hover {
	opacity: 1;
}

#search-user-header {
    width: 100%; 
    
    background:#2b3337;
}

@media (max-width: 768px) {
    .search-user-page {
        /* Everything */
        /* background: green; */
        display: flex;
        justify-content: center;
        align-items: center;

    }
    .search-user-page li {
        list-style: none;
    }
}

.organisation-img {
    width: 30px;
    border-radius: 25%;
    margin: 15px 5px 0 5px;
    opacity: 0.9;
    transition: 0.3s;
    display: inline;
}

#organisation-login {
    position: relative;
    bottom: 10px;
}

.organisation-img:hover {
    opacity: 1;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);

}

@media (max-width: 500px) {
    #organisation-login {
        font-size: 7.5px;
    }
    .organisation-img {
        width: 25px;
    }
}