@keyframes loading {
  0% {font-size: 20px; background: rgb(34, 34, 34);}
  100% {font-size: 45px;}
}

#loading { 
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 45px;

  animation-name: loading;
  animation-duration: 1.75s;
}

.no-info-avaiable { 
  display: flex;
  justify-content: center;
  align-items: center;
  height: 75vh;
  font-size: 17.5px;
  font-weight: 500;
}


