.person-grid-wrapper {
	background-color: #2b3137;
	/* background-color: rgba(43, 49, 55, 10); */
}

.person-grid-container {
	margin: 0 70px 20px 70px;
	max-height: 91.5vh;
	overflow: scroll;
	background-color: #ffffff;
	text-align: center;
}

.person-grid-name {
	color: #0d74e7;
	text-decoration: none;
	font-size: 30px;
	/* background: red; */
}

.person-grid-avatar {
	width: 83.5%;
	margin: 20px 20px 0 20px;
	border-radius: 50%;
}

.person-grid-type {
	color: rgb(219, 219, 219);
}

.person-grid-repos,
.no-info-avaiable {
	margin-top: 20px;
    list-style: none;
}

#grid-list-grid-switcher {
	display: flex;
	justify-content: center;
	align-items: flex-start;
	width: 3vw;
	height: 1vh;
	cursor: pointer;
}
#person-grid-list-of-persons {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-weight: 200;
    letter-spacing: 2px;
    font-size: 20px;
    height: 50px;
    background-color: #d8d8d8;
    color: #2b3337;
}

.person-grid-each-person {
	margin: 20px 5px 0 30px;
	max-width: 250px;
	background: #24292d;
	border-radius: 3px;
	color: rgb(224, 224, 224);
	display: inline-block;
}

@media only screen and (max-width: 768px) {
	.person-grid-each-person {
		zoom: 60%
	}
}

@media only screen and (max-width: 468px) {
    #person-grid-list-of-persons {
		height: 30vh;
	}
	.person-grid-each-person {
		max-width: 70;
		font-size: 13.5px;
	}
}


@media only screen and (max-width: 368px) {
    .person-grid-each-person {
		zoom: 55%;

    }
    #person-grid-list-of-persons {
        height: 60vh;
    }
}


  ::-webkit-scrollbar-thumb {
	background: #888;
  }
  
  